@import '../../styles/colors';

.Main2 {
    padding: 80px;
}

.section {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.expert__section {
    flex-direction: row-reverse;
}

.article {
    width: 600px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $body-text7;
}

.expertCard {
    background-color: $background2;
    height: 382px;
    width: 236px;
    padding: 26px;
    gap: 1.5em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    text-transform: uppercase;
    border: 3px solid $black1;
    box-shadow: 0 0 15px 5px rgba($black1, 0.15);
}

.business__aside {
    position: relative;
    height: 400px;
    width: 400px;
}

.ownerCard {
    position: absolute;
    top: 0;
    left: 0;
    &:nth-child(2) {
        transform: rotate(15deg) translate(80px, -10px);
        transform-origin: 170px 300px;
    }
}

.partners__section {
    padding: 80px 80px 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 50px;
}

.partners__article {
    width: 864px;
    flex-direction: column;
    gap: 36px;
}

.partnerButton {
    background-color: $black1;
    color: $white1;
    font-family: 'Futurabkbt', sans-serif;
    width: 190px;
    padding: 18px;
    font-size: 16px;
    border-radius: 2px;
    &:hover {
        color: $black1;
        background-color: $white2;
        cursor: pointer;
    }
}

.partnersWithUs {
    text-transform: uppercase;
    display: flex;
    align-items: center;
}

.partnersAvatars {
    display: flex;
    justify-content: space-between;
}

.partnersAvatar {
    width: 40px;
    height: 40px;
    border: 2px solid $white1;
    border-radius: 50%;
    @for $i from 1 through 6 {
        &:nth-child(#{$i+1}) {
            transform: translateX(-#{$i * 10}px);
        }
    }
}

.isInvestor {
    color: $body-text9;
    text-decoration: none;
    align-self: flex-start;
    margin-right: auto;
    strong {
        color: $black1;
    }
}