@import "../../styles/colors";

.Workshop1 {
    height: 500px;
    background-image: url(./workshopBG.png);
    background-position: center center;
    background-size: 60px;
    background-repeat: repeat;
    display: flex;
    align-items: end;
}

.content {
    height: calc(100% - 120px);
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 18px;
}

.bigLogo {
    color: $white1;
    font-weight: bold;
}

.description {
    color: $white1;
    font-size: 18px;
}
