@import "../../styles/colors";

.button {
    display: flex;
    justify-content: center;
    gap: 1em;
    border: none;
    box-shadow: none;
    transition: all 0.3s ease-out;
    padding: 18px;
    background-color: $white1;
    color: $black1;
    width: 190px;
    padding: 18px;
    font-size: 16px;
    border-radius: 2px;
    text-transform: uppercase;
    &:hover {
        cursor: pointer;
        transition: all 0.3s ease-out;
    }
}