@import '../../styles/colors';

.Footer {
    background-color: $black1;
    height: 300px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    padding: 0 80px 80px 80px;
}

.borderedLogo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 10px auto 80px auto;
    opacity: 0.5;
}

.borderLogo {
    width: 40%;
    border-top: 1px solid $white1;
}

.copyright {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: $white1;
    padding: 18px;
    gap: 12px;
}