@import '../../styles/colors';

.Team {
    padding-top: 120px;
    background-color: $black1;
}

.bigLogoContainer {
    width: 100%;
    height: 600px;
    padding: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
    z-index: 10;
    position: relative;
}

.globus {
    position: absolute;
    left: 50%; 
    transform: translateX(-50%) scale(1.3);
    z-index: 12;
}

.bigLogo {
    width: 650px;
    z-index: 10;  
    font-size: 96px;
    text-align: center;  
    color: $white1;
    font-weight: bold;
}

.teamDescription {
    color: $body-text9;
    text-align: center;
}

.teamMembers {
    background-color: $black1;
    margin: auto;
    display: grid;
    grid-template-columns: 33% 33% 33%;
    justify-content: center;
    align-items: center;
    width: fit-content;
    gap: 9px;
    padding: 80px 0;
}

.teamMember {
    display: block;
    width: 384px;
    height: 384px;
    background-color: $dark1;
    border: none;
}