@import '../../styles/colors';

.OwnerCard {
    width: 236px;
    height: 382px;
    border-width: 15px;
    padding: 20px;
    outline: 5px solid $black1;
    box-shadow: 0 0 15px 5px rgba($black1, 0.15);
    outline-offset: -20px;
    background-color: $white1;
}

.header {
    height: 82px;
    border-bottom: 5px solid $black1;
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    h5, h6 {
        text-transform: uppercase;
        text-align: center;
        font-weight: bold;
    }
    h5 {
        font-size: 18px;
    }
}

.content {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px;
    & > div {
        display: flex;
        gap: 10px;
    }
}

.house {
    width: 18px;
    height: 18px;
    background-color: $dark3;
}

.placeholder {
    height: 18px;
    width: 70px;
    background-color: $dark3;
}