@import "../../styles/colors";

.header {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    height: 120px;
    width: 100vw;
    background: $black1;
    border-bottom: 1px solid $white1;
    z-index: 9990;
}

.header__nav {
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    gap: 2em;
}

.header__links {
    display: flex;
    width: 40%;
    gap: 28px;
    color: $body-text6;
    padding: 0;
    margin: 0;
    list-style: none;
    text-decoration: none;
    a {
        color: $body-text6;
        text-decoration: none;
    }
    &_left {
        justify-content: flex-start;
    }
    &_right {
        justify-content: flex-end;
    }
}

.header__logo {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 18%;
}
