@import '../../styles/colors';

.Main1 {
    padding-bottom: 120px;
    background-color: $black1;
}

.content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 120px;
}

.bigLogoContainer {
    grid-area: mnplst;
    width: 100%;
    height: 600px;
    padding: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    position: relative;
}

.globus {
    position: absolute;
    left: 50%; 
    transform: translateX(-50%) scale(1.3);
    z-index: 12;
}

.bigLogo {
    width: 650px;
    height: 200px;
    z-index: 10;
}

.cards {
    width: 100%;
    padding: 80px 80px 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.idea {
    grid-area: hvidea;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1.5em;
    color: $body-text9;
    font-size: 18px;
    width: 50%;
}

.ideaText {
    width: 484px;
    text-align: center;
}

.ideaButton {
    background-color: $white1;
    color: $black1;
    font-family: 'Futurabkbt', sans-serif;
    width: 190px;
    padding: 18px;
    font-size: 16px;
    border-radius: 2px;
    &:hover {
        color: $white1;
        background-color: $black2;
        cursor: pointer;
    }
}

.slogan {
    grid-area: slogan;
    background-color: $white1;
    width: 382px;
    height: 236px;
    border-color: $black1;
    border-width: 15px;
    outline: 5px solid $black1;
    outline-offset: -20px;
    padding: 28px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 18px;
    text-transform: uppercase;
}

.sloganText {
    color: $black1;
}